@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200&display=swap');
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    height: 100vh;
    font-family: 'Source Sans Pro', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url('./images/background.jpg');
    background-size: 100%;
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

.main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.topDiv {
    display: flex;
    justify-content: center;
    align-items: center;
}

header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 15vh;
    width: 100%;
}

.main {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    height: 70%;
    width: 80%;
}

.drinkContent {
    display: flex;
    flex-direction: row-reverse;
    background: #E4D8DC;
    border-radius: 20px;
    box-shadow: 2px 5px 5px 2px #898b91;
    padding: 10px;
    width: 95%;
    height: 50%;
}

.drinkImg {
    height: 200px;
    width: 200px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.drinkContent img {
    height: 80%;
    width: 100%;
    border-radius: 20px;
    margin: 0 5px;
    box-shadow: 0 5px 5px #898b91;
}

.noneImg {
    width: 20%;
}

.instructionDiv {
    width: 50%;
}

#one {
    display: none;
}

#ingredients {
    display: none;
}

#instrcutionsDOM {
    display: none;
}

li {
    list-style-position: inside;
}